*
{
    margin: 0;
    padding:0;
}
.container
{
    background: linear-gradient(to right bottom, #e1b382,#c89666);
    width:100%;
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}
.app-wrapper
{
    background-color: #12343b;
    min-width: 450px;
    min-height:550px;
    padding: 30px;
    box-sizing: border-box;
    border-radius: 10px;
    box-shadow: 3px 6px 40px #000;
    margin-bottom: 10px;
}
.header h1
{
    color:tomato;
    margin: 30px 0px;
    text-align: center;
}
.task-input
{
    outline: none;
    width: 260px;
    padding:15px;
    margin-right:15px;
    font-size: 20px;
    color: #ccc;
    background-color: #000000;
    border:1px solid #c89666;
    border-radius: 10px;
}
.button-add
{
    width:90px;
    padding: 15px;
    font-size: 16px;
    background-color:#f1af71;
    border: 0;
    border-radius: 10px;
    cursor: pointer;color:green;
    font-weight: 700;
}
.list-item
{
    display: flex;
    margin: 20px 0px;
    border: 1px solid #ccc;
    border-radius: 10px;
    padding: 10px;
    max-height: 30px;
}
.button-complete,.button-edit,.button-delete
{
    border: 0;
    outline: 0;
    cursor: pointer;
    background: transparent;
    font-size: 28px;
    border-radius: 50%;
}
.button-complete
{
    color:#ff6c6c;
    margin-right: 10px;
}
.button-edit
{
    color:#e2d029;
    margin-right: 10px;
}
.button-delete
{
    color: lightseagreen;
}
.complete
{
    text-decoration-style:solid ;
    text-decoration-line: line-through;
    text-decoration-color: #ff6c6c;
    opacity: 0.6;
}
.list
{
    width:240px;
    background-color: #12343b;
    color: #ccc;
    border: 0;
    font-size: 20px;
    padding-left: 10px;
    margin-right: 15px;
}
input:focus,button:focus
{
    outline: 0;
}

@media screen and (max-width:500px)
{

.app-wrapper
{
    min-width: 300px;
    min-height:350px;
    padding:10px;
}
.header h1
{
    color:tomato;
    margin: 15px 0px;
    text-align: center;
    font-size: 20px;
}
.task-input
{   
    width: 200px;
    padding:10px;
    margin-right:10px;
    font-size: 16px;
}
.button-add
{
    width:75px;
    padding:10px;
    font-size: 13px;
    font-weight:400;
}
.list-item
{
    display: flex;
    margin: 10px 0px;
    border: 1px solid #ccc;
    border-radius: 10px;
    padding: 10px;
    max-height:20px;
}
.button-complete,.button-edit,.button-delete
{
    font-size: 18px;  
}
.list
{
    width:200px;
    background-color: #12343b;
    color: #ccc;
    border: 0;
    font-size: 16px;
    padding-left: 10px;
    margin-right: 15px;
}
}